.overflow-auto {
  overflow: auto; }

.ajax-is-loading {
  color: transparent;
  background: #00000011; }

.m-t-2 {
  margin-top: 2rem!important; }

.display-table {
  display: table;
  height: 101%;
  width: 100%; }

.display-table-cell {
  display: table-cell;
  vertical-align: middle; }

.display-table-cell__bottom {
  display: table-cell;
  vertical-align: bottom; }

