.article {
  margin: 2rem auto 4rem auto;
  max-width: 45rem;
  table {
    overflow-wrap: normal; }
  &--toc {
    max-width: 70rem; }
  .content {
    margin: 0 12px;
    > * {
      max-width: 100%; } } }

@include from(700px) {
  .article {
    margin: 2rem auto 4rem auto; } }


.imageHeader {
  position: relative;
  padding: 5rem 0;
  margin: 5rem 0 3rem 0;
  text-align: center;
  h2 {
    position: relative;
    z-index: 2;
    color: white;
    font-size: 3rem;
    margin: 0!important; }
  div {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin-left: -2000px;
    margin-right: -2000px;
    z-index: 1; }
  &__overlay {
    background: #123a50;//$primary
    opacity: 0.5; } }


// footer
.footer-groups {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center; }

.footer-column {
  flex: 0 1 250px;
  margin: 24px; }

.footer-column {
  @extend .menu; }
.footer-column-name {
  @extend .menu-label; }
.footer-links {
  @extend .menu-list;
  a {
    padding: 0.25em 0; } }

.footer-footer {
  margin-top: 24px;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  >div {
    margin: 0 24px; } }

.footer-hostedby {
  background: #242021;
  padding: 12px;
  text-align: center; }

.footer-online-list {
  margin-top: 12px;
  li {
    display: inline-block;
    font-size: 0.8rem;
    a {
      color: inherit; } } }


.archive-filter {
  display: flex;
  justify-content: center;
  background: white;
  border-bottom: 1px solid #00000014;
  >div {
    padding: 1em; } }

.content figure {
  margin-left: 1em;
  margin-right: 1em;
  text-align: left; }

img.inline-left {
  float: left;
  margin: 0 12px 12px 0; }

img.inline-right {
  float: right;
  margin: 12px 12px 0 0; }


.video-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  max-width: 100%;
  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; } }
